import React from "react";
import "./styles.css";
import star from "./star.png";
import arrow from "./arrow.png";

const DisplayNumber = props => (
  <button
    className={`number ${props.status}`}
    onClick={() => props.onClick(props.value, props.status)}
  >
    {props.value}
  </button>
);

const Star = props => (
  <img className="star" src={props.url} alt="Star" height="40" width="40" />
);

export default class App extends React.Component {
  constructor() {
    super();
    this.state = {
      isActive: false,
      timer: 10,
      availableNums: [1, 2, 3, 4, 5, 6, 7, 8, 9],
      selectedNums: [],
      randomStars: 9,
      selectedNumsSum: 0,
      gameStatus: '',
      toggle: false
    };
  }

  startGame = () => {
    const { availableNums, randomStars } = this.state;
    const newRandomStar = mathFunctions.generateStars(
      availableNums,
      9,
      randomStars
    );
    this.setState({
      isActive: true,
      randomStars: newRandomStar
    });
    this.timerInterval = setInterval(this.timerUpdate, 1000);
  };

  stopGame = (timer, randomStars, gameStatus) => {
    clearInterval(this.timerInterval);
    this.setState({
      isActive: false,
      timer,
      availableNums: [1, 2, 3, 4, 5, 6, 7, 8, 9],
      selectedNums: [],
      randomStars,
      selectedNumsSum: 0,
      gameStatus
    })
  };

  playAgain = () => {
    this.setState({
      isActive: false,
      timer: 10,
      availableNums: [1, 2, 3, 4, 5, 6, 7, 8, 9],
      selectedNums: [],
      randomStars: 9,
      selectedNumsSum: 0,
      gameStatus: ''
    })
  }

  timerUpdate = () => {
    const {timer, availableNums} = this.state;
    if(availableNums.length === 0)
    this.stopGame(timer, 0, 'You Won');
    else if(timer > 0)
    this.setState({
      timer: timer - 1
    });
    else
    this.stopGame(0, 0, 'You Lost');
  }

  numberStatus = value => {
    const {
      selectedNums,
      availableNums,
      randomStars,
      selectedNumsSum
    } = this.state;
    if (availableNums.includes(value)) return "available";
    else if (selectedNums.includes(value))
      return selectedNumsSum > randomStars ? "wrong" : "selected";
    return "used";
  };

  buttonClick = (val, status) => {
    if (!this.state.isActive || status === "used") return;
    let { selectedNums, availableNums, randomStars } = this.state;
    if (availableNums.includes(val)) {
      selectedNums.push(val);
      availableNums = availableNums.filter(
        availableVal => availableVal !== val
      );
    } else {
      availableNums.push(val);
      selectedNums = selectedNums.filter(selectedVal => selectedVal !== val);
    }
    const selectedNumsSum = mathFunctions.sum(selectedNums);
    if (selectedNumsSum === randomStars) {
      selectedNums = [];
      randomStars = mathFunctions.generateStars(
        availableNums,
        9,
        randomStars
      );
    }
    this.setState({
      selectedNums,
      availableNums,
      selectedNumsSum,
      randomStars
    });
  };

  toggle = () => this.setState({toggle: !this.state.toggle});

  nums = [1, 2, 3, 4, 5, 6, 7, 8, 9];
  render() {
    return (
      <div className="layout">
        <h1 className='title'>iT mAY sTAR aLL oVER yOU </h1>
        <div className="playArea">
          <div className="playArea-left" id='test'>
              {this.state.gameStatus !== '' && <div className="gameStatus">
                <h2 className='gameMessage'>{this.state.gameStatus}</h2>
                <button className='btn btn-md btn-warning' onClick={this.playAgain}>Play Again</button>
              </div>}
            {this.nums.map(value => {
              if (value <= this.state.randomStars)
                return <Star key={value} url={star} />;
              return null;
            })}
          </div>
          <div className="playArea-right">
            {this.nums.map(value => (
              <DisplayNumber
                key={value}
                value={value}
                status={this.numberStatus(value)}
                onClick={this.buttonClick}
              />
            ))}
          </div>
        </div>
        <span className='timer'>TIMER : {this.state.timer} sec. . .</span>
        {(this.state.gameStatus === '' && !this.state.isActive) && <a href='#test'><button className='btn btn-md btn-warning' onClick={this.startGame}>Start</button></a>}
        {this.state.isActive && <button className='btn btn-md btn-danger' onClick={() => this.stopGame(10, 9, '')}>Exit</button>}
        <div className='rules'>
          <div className='rules-title'>
            <h2>Game Play</h2>
            <img style={{cursor: 'pointer'}} src={arrow} alt="Show" onClick={this.toggle}/>
          </div>
          {this.state.toggle && 
          <div className="rules-content">
            <p style={{fontSize: '20px'}}>On the left of the play area random number of stars will be displayed.
              You will have to select either a number or set of numbers whose value will be equal
              to the number of stars present on the left.
            </p>
            <h3>Color Representation:</h3>
            <ul>
              <li>Default - Available</li>
              <li>Yellow - Used</li>
              <li>Blue - Currently selected</li>
              <li>Red - Wrong selection</li>
            </ul>
          </div>
          }
        </div>
      </div>
    );
  }
}

const mathFunctions = {
  sum: arr => arr.reduce((sum, val) => sum + val, 0),

  random: (min, max) => Math.floor(Math.random() * (max - min)) + min,

  generateStars: (arr, max, prevValue) => {
    let newArr = [];
    for (let i = 0; i < arr.length - 1; ++i)
      for (let j = i + 1; j < arr.length; ++j) {
        const temp = arr[i] + arr[j];
        if (temp <= max && !newArr.includes(temp) && temp !== prevValue) newArr.push(temp);
      }
      if(newArr.length === 0)
      newArr = [...arr];

    return newArr[mathFunctions.random(0, newArr.length - 1)];;
  }
};
